body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  padding: 50px 15px 0px;
}

header img {
  text-align: center;
}

header h1 {
  text-align: center;
  font-size: 48px;
  color: black;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 80%;
  padding: 15px;
  margin: auto;
  max-width: 80%;
  border: none;
  outline: none;
  background: none;
  background-color: black;
  border-radius: 8px;
  color: #F2B705;
  font-size: 20px;
  font-weight: 300;
  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 0px 3px;
}

.results {
  margin: 0px 15px 10px;
  display: flex;
  flex-wrap: wrap
}

.results .result {
  width: 33.333%;
  min-width: 250px;
  padding:  15px;
  display: flex;
  flex-direction: column;
}

.results .result img {
  display: block;
  width: 100%;
}

.results .result h3 {
  color: #F2B705;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: black;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
}

.results .result:hover h3 {
  background: #F2B705;
  color: #000000;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 40rem;
  height: 30em;
  background-color: rgba(13, 13, 13, 0.664);
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
  color: #F2B705;
}

.popup .content .year{
  color: #F2B705;
}

.popup .content .rating {
  margin-bottom: 30px;
  color: #F2B705;
}

.popup .content .plot {
  display: flex;
  margin:0px -15px 30px;
  color: white;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
  width: 30%;
}

.popup .content .plot p {
  padding: 5%;
}

.popup .close {
  border: none;
  background: none;
  font-size: 30px;
  display: flex;
  float: right;
  font-weight: bold;
  color: white;
}

.popup .close:hover {
  color: #F2B705;
}

.card-img, .card-img-top {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.IMBD-card .card {
  overflow-y: scroll;
  background-color: #F2CB05;
}

p .notfound {
  text-align: center;
}

.IMBD-card .card::-webkit-scrollbar {
  width: 12px;
}

.IMBD-card .card::-webkit-scrollbar-track {
  background: #F2CB05;
}

.IMBD-card .card::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 20px;
}

