/** Now Playing **/

.title {
  width: 230px;
  height: 100px;
}

.now-playing__name {
  font-size: 1.5em;
  margin-bottom: 0.2em;
}

.now-playing__artist {
  margin-bottom: 1em;
}

.now-playing__status {
  margin-bottom: 1em;
}

.now-playing__img {
  float: left;
  margin-right: 10px;
  text-align: right;
  width: 45%;
}

.now-playing__img img {
  max-width: 80vmin;
  width: 100%;
}

.now-playing__side {
  margin-left: 5%;
  width: 45%;
} /** Progress **/

.progress {
  background-color: rgb(88, 88, 88);
  height: 6px;
  border-radius: 5px;
  overflow: hidden;
}

.progress__bar {
  background-color:#1ed760;
  height: 4px;
}

/* Main wrapper */

.main-wrapper {
  align-items: center;
  display: flex;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  position: relative;
  width: 80%;
  z-index: 1;
}

.main-container {
  flex: 1 1;
}
.btn {
  /*margin-left:30px;*/
  background-color: #1ecd97;
  border-radius: 2em;
  color: #1ecd97;
  cursor: pointer;
  font-size: 3vmin;
  color: white;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
}

.btn:hover {
  background: #1ecd97;
  color: #333;
}

.btn--login {
  margin: 0 auto;
}
.displayweather {
  margin-top: 20px;
  display: flex;
  flex-flow: column nowrap;
}

.displayweather .maincard {
  width: 500px;
  margin-top: 20px;
  align-self: center;
  padding: 50px;
  border-radius: 20px;
  position: relative;
  background: rgb(255, 255, 255);
}

.displayweather .maincard .cardtitle {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1.5rem;
}

.displayweather .maincard .cardsubtitle {
  position: absolute;
  left: 10px;
  color: rgb(129, 127, 127);
  margin-top: -8px;
}

.displayweather h1 {
  font-size: 5rem;
  margin: -10px;
  margin-left: -150px;
}

.displayweather .weather-icon {
  width: 100px;
  position: absolute;
  top: 50px;
}

.displayweather .weather-main {
  position: absolute;
  top: 120px;
  left: 310px;
  font-weight: 500;
  font-size: 1.2rem;
}

.displayweather .weather-description {
  position: absolute;
  left: 10px;
  font-size: 1.4rem;
}

.displayweather .weatherdetails {
  margin-left: 50px;
  margin-right: 50px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-gap: 20px;
}

.displayweather .weatherdetails .section1 {
  justify-self: right;
  width: 300px;
}

h4 {
  text-align: left;
  margin-right: 30px;
  font-size: 1.2rem;
}

td {
  border-bottom: 2px solid rgb(255, 255, 255);
}

.displayweather .weatherdetails span {
  font-size: 1.2rem;
}
.weather {
  margin-top: 20px;
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  border-radius: 20px;
  padding-bottom: 30px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 0px 20px 20px 20px;
  margin-left: 25%;
}

.weather .title {
  width: 230px;
  height: 100px;
}

.weather form {
  margin-top: 30px;
}

.weather form input {
  padding: 10px;
  border-radius: 15px;
  border-bottom: 2px ;
  margin-right: 10px;
}

.weather form input:focus {
  outline: none;
}

.weather .getweather {
  margin-left: 10px;
  padding: 8px;
  font-size: 1.3rem;
  border: none;
  border-radius: 20px;
  background: rgba(52, 159, 221, 0.8);
  cursor: pointer;
  color: whitesmoke;
}

.weather .getweather:hover {
  background: transparent;
  color: black;
}

.weather .getweather:focus {
  outline: none;
}
