.button {
  background-color: rgba(0, 0, 0, 0.75);
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgba(0, 0, 0, 0.85);
}

.card-list {
  -webkit-column-count: 3;
  column-count: 3;
}

.card {
  margin-bottom: 1rem;
  display: flex;
}

.card--image {
  flex: 100% 1;
  margin-top: 1rem;
  border-radius: 10px;
}

@media (min-width: 768px) {
  .for {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }

  .inpu {
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 600px) {
  .card-list {
    -webkit-column-count: 1;
    column-count: 1;
  }
